
#sidebar{z-index:5;width:260px;border-right:1px solid #dbe2e8;box-shadow:inset 0 0 0 0 #dbe2e8,0 2px 1px 0 rgba(47,61,73,0.05);height:100%;visibility:visible !important;overflow-x:hidden;will-change:transform;transition-duration:.2s;transition-timing-function:cubic-bezier(0.4, 0, 0.2, 1);transition-property:transform;position:fixed
}
#sidebar.open{z-index:11111;transform:translate3d(0, 0, 0) !important
}
@media only screen and (min-width: 481px){
#sidebar{z-index:10000;transform:translate3d(-260px, 0, 0) !important
}
}
@media only screen and (max-width: 480px){
#sidebar{z-index:10000;transform:translate3d(-260px, 0, 0) !important
}
}
@media only screen and (min-width: 1025px){
#sidebar{transform:translate3d(0, 0, 0) !important
}
}
#sidebar .el-menu{height:100%;display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column
}
#sidebar .logo-container{text-align:center;width:100%
}
#sidebar .logo-container .logo{width:215px;height:215px
}



