
#app{color:#333;width:100%;height:100%
}
#app .page-layout{position:absolute;width:100%;height:100%
}
#app .page-layout main{z-index:1;overflow-x:auto;height:100%;padding-top:80px;position:absolute;transform-style:preserve-3d;will-change:transform;transition-property:transform;width:100%;transform:translate3d(0, 0, 0) !important
}
@media only screen and (min-width: 1025px){
#app .page-layout main{width:calc(100% - 260px);transform:translate3d(260px, 0, 0) !important
}
}
#app .page-layout main .main-content{width:100%;margin:0
}
@media only screen and (min-width: 481px){
#app .page-layout main .main-content{margin:0 auto
}
}
@media only screen and (max-width: 480px){
#app .page-layout main .main-content{margin:0 auto
}
}
#app .page-layout main .main-content .container{margin-top:0;max-width:1140px;padding:0 20px
}
#app .page-layout main .main-content .container>div:first-child{width:100%
}



