
header{z-index:6;background:#fff;border-bottom:1px solid #dbe2e8;box-shadow:inset 0 0 0 0 #dbe2e8,0 2px 1px 0 rgba(47,61,73,0.05);color:#2e3d49;position:fixed;transform-style:preserve-3d;will-change:transform;transition-duration:.2s;transition-timing-function:cubic-bezier(0.4, 0, 0.2, 1);transition-property:transform;width:100%
}
@media only screen and (min-width: 481px){
header{transform:translate3d(0, 0, 0) !important
}
}
@media only screen and (max-width: 480px){
header{transform:translate3d(0, 0, 0) !important
}
}
@media only screen and (min-width: 1025px){
header{width:calc(100% - 260px);transform:translate3d(260px, 0, 0) !important
}
}
header.no_sidebar{transform:translate3d(0, 0, 0) !important;width:100%
}
header .header-navicon{display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;-ms-flex-line-pack:center;align-content:center
}
@media only screen and (min-width: 1025px){
header .header-navicon{display:none
}
}
header .header-navicon i{color:#fff !important;font-size:2rem !important
}
header .header-menu{float:right !important
}
header .header-title{font-size:24px;color:#fff;margin-left:0;line-height:60px
}
@media only screen and (min-width: 1025px){
header .header-title{margin-left:24px
}
}



